* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.main {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.left-side img {
  height: 560px;
}
.right-side {
  width: 350px;
  height: 500px;
  border-radius: 5px;
  border: 1px solid rgba(177, 177, 177, 0.726);
  display: flex;
  flex-direction: column;
  align-items: center;
}
.right-side img {
  margin: 50px;
  width: 200px;
}
form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
form input {
  padding: 8px;
  width: 280px;
  /* border-radius: 3px; */
}
form button {
  padding: 10px;
  background-color: rgb(81, 81, 233);
  border: none;
  cursor: pointer;
  border-radius: 6px;
  color: #fff;
}
form p{
    text-align: center;
    color: gray;
    font-family: sans-serif;
    font-size: 0.8em;
}
form h5{
    text-align: center;
    font-size: 0.9em;
    color: rgba(2, 2, 131, 0.781);
    font-family: sans-serif;
    cursor: pointer;
}
form a{
    text-align: center;
    margin: 10px;
    text-decoration: none;
}


@media only screen and (max-width: 600px) {
   .left-side{
    display: none;
   }
  }